import * as React from "react";

import Web3 from "web3";

import contracts, { INFURA_KEY } from "../lib/contracts";
import Header from "./Header";
import Loading from "./Loading";

import { Token, TokenDetails, Tokens } from "../lib/tokens";
import { FeesBlock } from "./FeesBlock";
import { StatusBlock } from "./StatusBlock";
import { TokenBalance } from "./TokenBalance";
import { Topup } from "./Topup";

interface StatusProps {
}

interface StatusState {
    network: string;
    multiAddress: string;
    darknodeAddress: string;
    publicKey: string;
    peers: number;
    success: boolean;
    registrationStatus: string;
    minBond: number;
    balances: BalanceItem[];
    currentBalance: number;
    error: boolean;
    errorMessage: JSX.Element | null;
    refreshing: boolean;
    correctNetwork: boolean;
}

interface BalanceItem {
    token: Token;
    balance: string;
}

export class StatusPage extends React.Component<StatusProps, StatusState> {
    private web3: Web3;
    private balanceInterval: any;

    constructor(props: StatusProps) {
        super(props);
        this.state = {
            network: "",
            multiAddress: "",
            darknodeAddress: "",
            publicKey: "",
            peers: 0,
            success: false,
            registrationStatus: "",
            minBond: 0,
            balances: [],
            currentBalance: 0,
            error: false,
            errorMessage: null,
            refreshing: false,
            correctNetwork: true
        };
        this.refreshInfo = this.refreshInfo.bind(this);
    }

    public async componentDidMount(): Promise<void> {
        // Retrieve Web3 from provider otherwise instantiate using Infura.
        const win = (window as any);
        if (typeof win.web3 !== "undefined") {
            this.web3 = new Web3(win.web3.currentProvider);
            const network = await (this.web3.eth.net as any).getNetworkType();
            if (network !== window.DARKNODE.ethereumNetwork) {
                this.setState({ correctNetwork: false });
            }
        } else {
            this.web3 = new Web3(new Web3.providers.HttpProvider(`${window.DARKNODE.infura}/${INFURA_KEY}`));
        }

        Promise.all(
            [
                this.getMinBond(),
                this.getDarknodeStatus(),
                this.getBalances(),
                this.web3.eth.getBalance(window.DARKNODE.ethereumAddress),
            ])
            .then((res) => {
                const minBond = res[0];
                const registrationStatus = res[1];
                const balances = res[2];
                const currentBalance = res[3];

                this.setState({
                    network: window.DARKNODE.network,
                    multiAddress: window.DARKNODE.multiAddress,
                    darknodeAddress: window.DARKNODE.ethereumAddress,
                    publicKey: window.DARKNODE.publicKey,
                    peers: window.DARKNODE.peers,
                    success: true,
                    registrationStatus,
                    minBond,
                    balances,
                    currentBalance,
                }, () => {
                    // Refresh darknode information every 10 seconds.
                    const callRefreshInfo = async () => {
                        try {
                            await this.refreshInfo();
                        } catch (err) {
                            console.error(err);
                        }
                        setTimeout(callRefreshInfo, 10 * 1000);
                    };
                    callRefreshInfo().catch(console.error);
                });
            })
            .catch((err) => {
                console.error(err);
            });
    }

    public async componentDidUpdate(): Promise<void> {
        if (!this.balanceInterval && this.state.darknodeAddress) {
            this.balanceInterval = setInterval(async () => {
                const currentBalance = await this.web3.eth.getBalance(this.state.darknodeAddress);
                if (currentBalance !== this.state.currentBalance) {
                    this.setState({ currentBalance });
                }
            }, 5000);
        }
    }

    public render(): JSX.Element {
        return (
            <div className="container">
                <Header />
                {
                    this.state.error ?
                        this.state.errorMessage
                        :
                        this.state.success && this.web3 !== undefined ?
                            <div className="content">
                                <div>
                                    <StatusBlock web3={this.web3} registrationStatus={this.state.registrationStatus} publicKey={this.state.publicKey} network={this.state.network} multiAddress={this.state.multiAddress} darknodeAddress={this.state.darknodeAddress} peers={this.state.peers} minBond={this.state.minBond} />
                                </div>
                                <div>
                                    <h1>Balance</h1>
                                    <TokenBalance token={Token.ETH} amount={this.state.currentBalance.toString()} min={0} />
                                    <h1>Top-up Balance</h1>
                                    <Topup web3={this.web3} network={this.state.network} darknodeAddress={this.state.darknodeAddress} />
                                    <h1>Fees Earned</h1>
                                    {
                                        this.state.balances.map((item: BalanceItem, key: number) => {
                                            return (
                                                <FeesBlock key={key} web3={this.web3} token={item.token} amount={item.balance} darknodeAddress={this.state.darknodeAddress} network={this.state.network} />
                                            );
                                        })
                                    }
                                </div>
                            </div>
                            : !this.state.correctNetwork ?
                                <p>Please ensure you are on the {window.DARKNODE.ethereumNetwork} network.</p>
                                :
                                <Loading fixed />
                }
            </div>
        );
    }

    private async refreshInfo(): Promise<void> {
        this.setState({ refreshing: true });
        return await Promise.all(
            [
                this.getDarknodeStatus(),
                this.getBalances(),
                this.web3.eth.getBalance(this.state.darknodeAddress),
            ])
            .then((res) => {
                const registrationStatus = res[0];
                const balances = res[1];
                const currentBalance = res[2];
                this.setState({
                    peers: window.DARKNODE.peers,
                    registrationStatus,
                    balances,
                    currentBalance,
                    refreshing: false
                });
            })
            .catch((err) => {
                console.error(err);
            });
    }

    private async getBalances(): Promise<BalanceItem[]> {
        const contract = new this.web3.eth.Contract(contracts.DarknodeRewardVault.ABI, contracts.DarknodeRewardVault.address);
        const balances = Tokens.map(async (token: Token) => {
            const tokenDetails = TokenDetails.get(token);
            const balance = await contract.methods.darknodeBalances(window.DARKNODE.ethereumAddress, tokenDetails.address).call();
            return {
                token,
                balance
            };
        });
        const res = await Promise.all(balances);
        return res;
    }

    private async getDarknodeStatus(): Promise<string> {
        const contract = new this.web3.eth.Contract(contracts.DarknodeRegistry.ABI, contracts.DarknodeRegistry.address);
        return new Promise<string>((resolve) => {
            Promise.all([
                contract.methods.isPendingRegistration(window.DARKNODE.ethereumAddress).call(),
                contract.methods.isPendingDeregistration(window.DARKNODE.ethereumAddress).call(),
                contract.methods.isDeregisterable(window.DARKNODE.ethereumAddress).call(),
                contract.methods.isRefunded(window.DARKNODE.ethereumAddress).call(),
                contract.methods.isRefundable(window.DARKNODE.ethereumAddress).call(),
                contract.methods.isRegistered(window.DARKNODE.ethereumAddress).call(),
            ]).then((response) => {
                const res = {
                    isPendingRegistration: response[0],
                    isPendingDeregistration: response[1],
                    isDeregisterable: response[2],
                    isRefunded: response[3],
                    isRefundable: response[4],
                    isRegistered: response[5],
                };
                let registrationStatus = "";
                if (res.isRefunded) {
                    registrationStatus = "unregistered";
                } else if (res.isPendingRegistration) {
                    registrationStatus = "registrationPending";
                } else if (res.isDeregisterable) {
                    registrationStatus = "registered";
                } else if (res.isPendingDeregistration) {
                    registrationStatus = "deregistrationPending";
                } else if (res.isRefundable) {
                    registrationStatus = "awaitingRefund";
                }
                resolve(registrationStatus);
            });
        });
    }

    private async getMinBond(): Promise<number> {
        const dnrContract = new this.web3.eth.Contract(contracts.DarknodeRegistry.ABI, contracts.DarknodeRegistry.address);
        return dnrContract.methods.minimumBond().call();
    }
}



